/*NAVBAR*/
.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #E2DCD4;
    padding: 10px;
    align-items: center;
}

.menu {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    align-items: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

ul {
    list-style-type: none;
}

ul :hover {
    text-decoration: underline;
    color: #0B0B10;
    font-size: 25px;
}

.menu-link {
    text-decoration: none;
    color: #BB191F;
    font-size: 23px;
    border-radius: 5px;
    padding: 5px;

}

/*CARRITO*/

.carro {
    display: flex;
    flex-direction: row;
    gap: 0.3rem;
}

.numeroCarro {
    font-size: 16px;
}

.numeroCarro:hover {
    font-size: 16px;
}