/*ITEM DETAIL*/

.container {
    display: flex;
    width: 100%;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.producto-detalle {
    display: flex;
    flex-direction: row;
    gap: 5rem;
    background-color: white;
    padding: 2rem;
    border-radius: 15px;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 1000px;
    height: 600px;
}

.card-img {
    object-fit: cover;
    object-position: top;
}

.product-img {
    max-height: 400px;
    object-fit: cover;
    object-position: top;
}

.titulo {
    text-align: center;
    font-size: 22px;
    font-family: Proxima Nova, -apple-system, Roboto, Arial, sans-serif;
    font-weight: 600;
}

.descripcion {
    font-size: 15px;
    font-family: Proxima Nova, -apple-system, Roboto, Arial, sans-serif;
    font-weight: 400;
}

.categoria {
    font-size: 15px;
    font-family: Proxima Nova, -apple-system, Roboto, Arial, sans-serif;
    font-weight: 400;
    color: #585858;
}

.descripcion-producto {
    padding: 0;
    font-size: 20px;
    font-family: Proxima Nova, -apple-system, Roboto, Arial, sans-serif;
    font-weight: 600;
    color: #353535;
}

.container-info {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 3rem;
    padding-right: 3rem;

}

.precio {
    font-size: 2rem;
    margin-top: 2rem;
    font-weight: 600;
    font-family: AktivGrotesk Bold, arial, sans-serif;
}

/*------------------------------------*/


.item-count {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
    font-family: AktivGrotesk Bold, arial, sans-serif;
}

.item-count button,
.add-to-cart,
.enviar {
    background-color: var(--clr-main);
    border: 0;
    width: 2rem;
    height: 2rem;
    color: #BB191F;
    background-color: #E2DCD4;
    font-family: AktivGrotesk Bold, arial, sans-serif;
    border-radius: .5rem;
    cursor: pointer;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    line-height: 1;
    border: 2px solid var(--clr-main);
    transition: .2s;
}

.item-count button:hover,
.add-to-cart:hover,
.enviar:hover {
    background-color: #E2DCD4;
    color: black;
}

.add-to-cart,
.enviar {
    width: auto;
    height: auto;
    margin-top: 1rem;
    padding: 1rem;
}