/*3-ITEM LIST CONTAINER (productos/inicio)*/

/*3.1-ITEM LIST*/
.container-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    border: 1px solid red;
}


.main-title {
    width: 70%;
    font-family: Proxima Nova, -apple-system, Helvetica Neue, Helvetica, Roboto, Arial, sans-serif;
    font-size: 26px;
    font-weight: normal;
    align-items: start;
}

.productos {
    display: grid;
    /* Espacio entre las celdas */
    grid-template-columns: repeat(4, 200px);
    /* 4 columnas de 200px cada una */
    grid-template-rows: repeat(5, 150px);
    /* 5 filas de 150px cada una */
    grid-column-gap: 10rem;
    grid-row-gap: 25rem;
    margin-bottom: 5rem;
}

.categorias2 {
    display: grid;
    /* Espacio entre las celdas */
    grid-template-columns: repeat(4, 200px);
    /* 4 columnas de 200px cada una */
    grid-template-rows: repeat(1, 150px);
    /* 5 filas de 150px cada una */
    grid-column-gap: 10rem;
    grid-row-gap: 25rem;
    margin-bottom: 13rem;
}

.categorias3 {
    display: grid;
    /* Espacio entre las celdas */
    grid-template-columns: repeat(4, 200px);
    /* 4 columnas de 200px cada una */
    grid-template-rows: repeat(2, 150px);
    /* 5 filas de 150px cada una */
    grid-column-gap: 10rem;
    grid-row-gap: 25rem;
    margin-bottom: 10rem;
}

/*3.2-ITEM*/
.producto-box {
    width: 12vw;
    height: 30rem;
    display: flex;
    justify-content: center;
    border-radius: 10px;
    align-items: center;
    padding: 1rem;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
    background-color: #ffffff;
}

.producto-box:hover {
    transform: scale(1.05);
}

.producto {
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: inherit;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;
}

.productos-card-img {
    position: relative;
    max-width: 12rem;
    height: 15rem;
    object-fit: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.productos-img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
    width: 100%;
    /* Ajusta el tamaño según sea necesario */
    height: auto;
    /* Ajusta el tamaño según sea necesario */
    display: block;
}

.oferta-img {
    position: absolute;
    top: 0;
    /* Ajusta la posición vertical según sea necesario */
    left: 0;

}

.offer-img {

    width: 50px;
    /* Ajusta el tamaño según sea necesario */
    height: auto;
}

.productos-detalles {
    margin-top: 1rem;
    border-top: 1px solid #ededed;
}

.fuente {
    font-family: Proxima Nova, -apple-system, Roboto, Arial, sans-serif;
}

.productos-title {
    font-size: 14px;
    font-weight: 600;
    color: rgb(51, 51, 51);
}

.productos-description {
    color: rgb(79, 79, 79);
    font-size: 15px;
    font-weight: 300;
}

.productos-category {
    font-size: 12px;
    font-weight: 300;
    color: rgb(102, 102, 102);
}

.precio-original {
    text-decoration: line-through;
    color: rgb(153, 153, 153);
    font-size: 12px;
    font-weight: 400;
}

.precio-con-descuento {
    color: rgb(51, 51, 51);
    font-size: 24px;
    font-weight: 400;
    padding: 0;
}

.off {
    color: rgb(23, 179, 129);
    font-size: 13px;
    font-weight: 300;
}

.precio-off {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.7rem;
    padding: 0;
}

.ahorro {
    display: flex;
    justify-content: center;
    color: rgb(51, 51, 51);
    font-size: 15px;
    font-weight: 400;
    width: 100%;
}